import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";

import { environment } from "../../../environments/environment";
import { Subject } from "rxjs";
import { map } from "rxjs/operators";
import { DatePipe } from "@angular/common";

@Injectable({ providedIn: "root" })
export class BeneficiaryService {
    access_token = localStorage.getItem("token");
    headers = new HttpHeaders({
        "Content-Type": "application/json",
        Authorization: "Bearer " + this.access_token,
    });
    options = { headers: this.headers };
    beneficiary = new Subject<any>();
    beneficiaries = new Subject<any>();

    constructor(private http: HttpClient, private datePipe: DatePipe) {}

    public getBeneficiariesData() {
        return this.beneficiaries.asObservable();
    }

    public getBeneficiaryData() {
        return this.beneficiary.asObservable();
    }

    get() {
        return this.http
            .get<any>(`${environment.apiUrl}/api/beneficiary`, this.options)
            .pipe(
                map((transfers) => {
                    this.beneficiaries.next(transfers);
                    return transfers;
                })
            );
    }

    findById(beneficiary_id: any) {
        return this.http
            .get<any>(
                `${environment.apiUrl}/api/beneficiary/${beneficiary_id}`,
                this.options
            )
            .pipe(
                map((beneficiary) => {
                    this.beneficiary.next(beneficiary);
                    return beneficiary;
                })
            );
    }
}
