import { Component, OnInit } from "@angular/core";
import { routerTransition } from "../../router.animations";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { BeneficiaryService } from "src/app/shared/services/benefiaciary.service";
import { InfoComponent } from "./info/info.component";

@Component({
    selector: "app-beneficiary",
    templateUrl: "./beneficiary.component.html",
    styleUrls: ["./beneficiary.component.scss"],
    animations: [routerTransition()],
})
export class BeneficiaryComponent implements OnInit {
    beneficiaries: any;
    beneficiary: any;

    constructor(
        private dialogService: NgbModal,
        private beneficiaryService: BeneficiaryService
    ) {}

    ngOnInit() {
        this.beneficiaryService.get().subscribe(() => {});
        this.beneficiaryService.getBeneficiariesData().subscribe((data) => {
            this.beneficiaries = data;
        });
    }

    info(beneId: any) {
        const modalRef = this.dialogService.open(InfoComponent, {
            size: "lg",
            centered: true,
        });
        modalRef.componentInstance.beneId = beneId;
    }
}
